.page-header {
  width: 1170px;
  margin: 0 auto;
  padding: 20px 55px;
  margin-bottom: 40px;
  background-color: rgba(0, 0, 0, 0.1);
}

.wrapper {
  display: flex;
}


.page-header__logo {
  margin: 0;
  margin-right: 173px;
  margin-left: 120px;
  width: 147px;
  height: 40px;
}

@media (max-width: 770px) {
  .page-header__logo {
    margin-left: 0;
    margin-right: 0;
  }

}

.main-nav {
  display: flex;
  align-items: center;
}

@media (max-width: 770px) {
  .main-nav {
    display: none;

  }
}

#toggle {
  display: none;
}

.hamburger,
.hamburger div {
  display: none;
}

/**
  Hamburger
**/










.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  margin-right: 40px;
  list-style: none;
  max-width: 990px;


}

.site-list__item {

  width: 131px;
  list-style: none;
  flex-wrap: wrap;

  a {
    color: #FFF;
    text-decoration: none;
  }
}
